import { makeStyles } from '@material-ui/core/styles'
import HomeIcon from '@material-ui/icons/Home'
import MailIcon from '@material-ui/icons/Mail'
import MenuIcon from '@material-ui/icons/Menu'
import NextWeekIcon from '@material-ui/icons/NextWeek'
import SportsHandballIcon from '@material-ui/icons/SportsHandball'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import React from 'react'

const actions = [
  { icon: <HomeIcon style={{ color: '#1c1c25' }} />, name: 'HOME' },
  { icon: <SportsHandballIcon style={{ color: '#1c1c25' }} />, name: 'SKILLS' },
  { icon: <NextWeekIcon style={{ color: '#1c1c25' }} />, name: 'MY WORKS' },
  { icon: <MailIcon style={{ color: '#1c1c25' }} />, name: 'CONTACT' },
]

export default function SpeedDialTooltipOpen() {
  const [open, setOpen] = React.useState(false)

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <SpeedDial
      ariaLabel="SpeedDial tooltip example"
      hidden={false}
      icon={<MenuIcon />}
      onClose={handleClose}
      onOpen={handleOpen}
      open={open}
      direction={'down'}
      FabProps={{ style: { background: 'none' } }}
    >
      {actions.map((action) => (
        <SpeedDialAction
          key={action.name}
          icon={action.icon}
          tooltipTitle={<span>{action.name}</span>}
          tooltipOpen={false}
          onClick={handleClose}
          tooltipPlacement={'right'}
        />
      ))}
    </SpeedDial>
  )
}
