import { styled } from '@material-ui/core/styles'
import React from 'react'
import chip from './images/chip.png'
import notrealsign from './images/notrealsign.jpg'

// const cardSize = { width: 'calc(508px*(50vw/100vw))', height: 'calc(314px*(50vw/100vw))' }

const cardSize = { width: 'calc(508px * 0.6)', height: 'calc(314px * 0.6)' }
const scale = 0.6

const StyledCard = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  width: cardSize.width,
  height: cardSize.height,
  transformStyle: 'preserve-3d',
  perspective: 'calc(500px * 0.6)',
  transition: '1s',
  // '&:hover': {
  //   transform: 'rotateY(180deg)',
  // },
})

const StyledCardFaceFront = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: '0',
  left: '0',
  width: cardSize.width,
  height: cardSize.height,
  boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
  borderRadius: '15px',
  background: 'rgba(255,255,255,0.05)',
  backdropFilter: 'blur(10px)',
  transformStyle: 'preserve-3d',
  transition: '1s',
  backfaceVisibility: 'hidden',
  // transform: 'rotateY(180deg)',
  // '&:hover': {
  //   transform: 'rotateY(180deg)',
  // },

  '&::before': {
    content: '""',
    position: 'absolute',
    bottom: `calc(40px * ${scale})`,
    right: `calc(40px * ${scale})`,
    width: `calc(60px * ${scale})`,
    height: `calc(60px * ${scale})`,
    background: '#fff',
    borderRadius: '50%',
    opacity: '0.5',
  },
  '&::after': {
    content: '""',
    position: 'absolute',
    bottom: `calc(40px * ${scale})`,
    right: `calc(80px * ${scale})`,
    width: `calc(60px * ${scale})`,
    height: `calc(60px * ${scale})`,
    background: '#fff',
    borderRadius: '50%',
    opacity: '0.7',
  },
})

const StyledDebit = styled(({ color, ...other }) => <h6 {...other} />)({
  position: 'absolute',
  left: `calc(40px * ${scale})`,
  top: `calc(30px * ${scale})`,
  color: '#fff',
  fontWeight: '500',
})

const StyledBankName = styled(({ color, ...other }) => <h6 {...other} />)({
  position: 'absolute',
  right: `calc(40px * ${scale})`,
  top: '-39px',
  color: '#fff',
  fontWeight: '500',
  fontStyle: 'italic',
  fontSize: `calc(40px * ${scale})`,
})

const StyledChip = styled(({ color, ...other }) => <img src={chip} alt="chip" {...other} />)({
  position: 'absolute',
  left: `calc(55px * ${scale})`,
  top: `calc(90px * ${scale})`,
  maxWidth: `calc(64px * ${scale})`,
})

const StyledNumber = styled(({ color, ...other }) => <h6 {...other} />)({
  position: 'absolute',
  bottom: `calc(90px * ${scale})`,
  left: `calc(40px * ${scale})`,
  color: '#fff',
  fontWeight: '500',
  letterSpacing: `calc(6px * ${scale})`,
  fontSize: `calc(18px * ${scale})`,
  textShadow: '0 2px 1px #0005',
  fontFamily: '"Orbitron", sans-serif',
})

const StyledValid = styled(({ color, ...other }) => <h6 {...other} />)({
  position: 'absolute',
  bottom: `calc(60px * ${scale})`,
  left: `calc(40px * ${scale})`,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  color: '#fff',
  fontWeight: '300',
  lineHeight: `calc(1em * ${scale})`,
  textAlign: 'right',
})

const StyledSpan = {
  marginLeft: `calc(20px * ${scale})`,
  fontSize: `calc(16px * ${scale})`,
  fontWeight: '400',
  letterSpacing: `calc(2px * ${scale})`,
}

const StyledHolder = styled(({ color, ...other }) => <h5 {...other} />)({
  position: 'absolute',
  bottom: '0px',
  left: `calc(40px * ${scale})`,
  color: '#fff',
  fontWeight: '300',
  fontSize: `calc(30px * ${scale})`,
  letterSpacing: `calc(2px * ${scale})`,
})

const StyledCardFaceBack = styled(({ color, ...other }) => <div {...other} />)({
  backdropFilter: 'blur(10px)',
  position: 'absolute',
  top: '0',
  left: '0',
  width: cardSize.width,
  height: cardSize.height,
  boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
  borderRadius: '15px',
  background: 'rgba(255,255,255,0.05)',
  transformStyle: 'preserve-3d',
  transition: '1s',
  transform: 'rotateY(180deg)',
  backfaceVisibility: 'hidden',
  '&:hover': {
    transform: 'rotateY(180deg)',
  },
})

const StyledBlackBar = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: `calc(40px * ${scale})`,
  width: '100%',
  height: `calc(60px * ${scale})`,
  background: '#000',
})

const StyledCCVText = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: `calc(80px * ${scale})`,
  left: `calc(30px * ${scale})`,
})

const StyledCVV = styled(({ color, ...other }) => <h5 {...other} />)({
  color: '#fff',
  fontWeight: '400',
  fontSize: `calc(12px * ${scale})`,
  textTransform: 'uppercase',
  letterSpacing: `calc(2px * ${scale})`,
})

const StyledWhiteBar = styled(({ color, ...other }) => <div {...other} />)({
  position: 'relative',
  width: `calc(400px * ${scale})`,
  height: `calc(40px * ${scale})`,
  background: '#fff',
  marginTop: `calc(-15px * ${scale})`,
})

const StyledCCV = styled(({ color, ...other }) => <div {...other} />)({
  position: 'relative',
  top: `calc(-35px * ${scale})`,
  left: `calc(395px * ${scale})`,
  background: '#ccc',
  width: `calc(50px * ${scale})`,
  height: `calc(30px * ${scale})`,
  fontWeight: '600',
  letterSpacing: `calc(3px * ${scale})`,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
})

const StyledText = styled(({ color, ...other }) => <p {...other} />)({
  position: 'absolute',
  bottom: `calc(6px * ${scale})`,
  left: `calc(30px * ${scale})`,
  right: `calc(30px * ${scale})`,
  color: '#fff',
  fontSize: `calc(10px * ${scale})`,
  lineHeight: `calc(2em * ${scale})`,
  fontWeight: '300',
  fontFamily: '"Kanit", sans-sarif',
})

const StyledNotRealSign = styled(({ color, ...other }) => (
  <img src={notrealsign} alt="chip" {...other} />
))({
  position: 'relative',
  width: `calc(150px * ${scale})`,
  left: `calc(116px * ${scale})`,
})

export function TransparentCard() {
  return (
    <StyledCard className="card">
      <StyledCardFaceFront className="front">
        <StyledDebit>Credit Card</StyledDebit>
        <StyledBankName>PNL Good Code</StyledBankName>
        <StyledChip />
        <StyledNumber>5692 8956 9289 5692</StyledNumber>
        <StyledValid>
          <span>
            Valid
            <br />
            thru
          </span>
          <span style={StyledSpan}>06/25</span>
        </StyledValid>
        <StyledHolder>Chatbordin Klinsrisuk</StyledHolder>
      </StyledCardFaceFront>

      <StyledCardFaceBack className="back">
        <StyledBlackBar />
        <StyledCCVText>
          <StyledCVV>Authorized Signature-not valid unless signed</StyledCVV>
          <StyledWhiteBar>
            <StyledNotRealSign />
          </StyledWhiteBar>
          <StyledCCV>651</StyledCCV>
        </StyledCCVText>
        <StyledText>
          วิศวกรไฟฟ้าที่เพิ่งรู้ว่าตัวเองชอบเขียนโค้ดตอนอายุ 30 ปีบริบูรณ์
          เขาเคยขายโปรแกรมได้ด้วยตัวเองครั้งแรกในราคา 39 บาทด้วยภาษา Python
          และต่อมาเขาเริ่มพัฒนาฝีมือในการเขียนเว็บด้วย NodeJS จนสามารถทำเงินได้ 78 บาท/เดือน
          ปัจจุบันกำลังฝึกเขียน ReactJS อย่างหนักหน่วง เพราะเขาตั้งใจว่าจะเป็นคนที่เขียน React
          เก่งที่สุดในไทย ฉัตรบดินทร์เป็นคนที่เชื่อว่าโชคชะตาเป็นสิ่งที่ถูกกำหนดมาแล้ว
          แต่เราสามารถกำหนดมันใหม่ได้เสมอ และเมื่อไม่นานมานี้
          ศาสตร์แห่งตัวเลขได้ทำให้เขามีความเชื่อในพุทธศาสนาอย่างหาที่สุดไม่ได้
        </StyledText>
      </StyledCardFaceBack>
    </StyledCard>
  )
}
